import React, { PureComponent } from 'react'
import ReactDOM from 'react-dom'
import { hot } from 'react-hot-loader'
import './index.less'
import ListView, { IListData, IProps as IListViewProps } from './ListView/index'
import registerServiceWorker from './registerServiceWorker'

let index = 0

function renderListData(arr: any[] = []) {
  return arr.concat(Array(30).fill(0).map(() => {
    index++
    return {
      key: index,
      title: index,
    }
  }))
}

interface IState {
  listData: IListData[],
  recycledAheadData: IListData[],
  recycledBehindData: IListData[],
}

class App extends PureComponent<any, IState> {
  public state = {
    listData: renderListData(),
    recycledAheadData: [],
    recycledBehindData: [],
  }

  private firstUpload = 1

  public onStartReached = () => {
    const {listData, recycledAheadData, recycledBehindData} = this.state
    const popCount = recycledAheadData.length > 30 ? 30 : recycledAheadData.length
    const popData = recycledAheadData.splice(recycledAheadData.length - popCount, popCount)
    const recycledData = listData.splice(listData.length - popCount, popCount)
    if (recycledAheadData.length === 0) {
      this.firstUpload = 1
    }
    this.setState({
      listData: [...popData, ...listData],
      recycledAheadData,
      recycledBehindData: [...recycledData, ...recycledBehindData]
    })
  }

  public onEndReached = () => {
    const {listData, recycledAheadData, recycledBehindData} = this.state
    const recycleNum: number = this.firstUpload ? 15 : 30
    const newData = recycledBehindData.length > 0 ? [...listData, ...recycledBehindData.splice(0, recycleNum)] :renderListData(listData)
    let newRecycledData: IListData[] = [...recycledAheadData]
    this.firstUpload = 0
    if (newData.length > 30) { newRecycledData = [...recycledAheadData, ...newData.splice(0, recycleNum)]}
    this.setState({
      listData: newData,
      recycledAheadData: newRecycledData,
      recycledBehindData,
    })
  }

  public render() {
    return <ListView
      data={this.state.listData}
      onEndReached={this.onEndReached}
      onStartReached={this.onStartReached}
      renderItem={this.renderItem}
    />
  }

  public renderItem: IListViewProps['renderItem'] = ({item}): JSX.Element => {
    return <li
      key={item.key}
      style={{
        backgroundColor: item.title % 2 ? '#999' : '#ccc',
        height: item.title % 3 * 100 || 30,
      }}>{item.title}</li>
  }
}

const APP = hot(module)(() => <App/>)

ReactDOM.render(
  <APP/>,
  document.getElementById('root') as HTMLElement,
)
registerServiceWorker()



