import React, { PureComponent } from 'react'
import s from './index.less'

export interface IProps {
  data: IListData[],
  height?: number,
  onStartReachedThreshold?: number,
  onEndReachedThreshold?: number,

  renderItem(data: { item: IListData, index: number }): JSX.Element,

  onStartReached?(): void,

  onEndReached?(): void,

  onScroll?(e: React.SyntheticEvent['currentTarget']): void,
}

interface IDefaultProps {
  height: number,
  onStartReachedThreshold: number
  onEndReachedThreshold: number

  onStartReached(): void,

  onEndReached(): void,

  onScroll(): void,
}

export interface IListData {
  key: string | number,

  [x: string]: any
}

type PropsWithDefault = IProps & IDefaultProps

export default class ListView extends PureComponent<PropsWithDefault> {
  public static defaultProps: IDefaultProps = {
    height: window.innerHeight,
    onEndReached: () => null,
    onEndReachedThreshold: 300,
    onScroll: () => null,
    onStartReached: () => null,
    onStartReachedThreshold: 300,
  }

  public render() {
    const {data, renderItem, height} = this.props
    return (
      <div
        className={s.container}
        style={{height}}
        onScroll={this.handleScroll}>
        <ul className={s.list}>
          {data.map((i, index) => {
            return renderItem({
              index,
              item: i,
            })
          })}
        </ul>
      </div>
    )
  }

  private handleScroll = (e: React.SyntheticEvent) => {
    const {onEndReached, onStartReached, onScroll, onEndReachedThreshold, onStartReachedThreshold} = this.props
    const {scrollTop, scrollHeight, clientHeight} = e.currentTarget

    // 判断是否触发onEndReached事件
    if (scrollHeight - clientHeight - scrollTop <= onEndReachedThreshold) {
      onEndReached()
    }

    if (scrollTop <= onStartReachedThreshold) {
      onStartReached()
    }

    onScroll(e.currentTarget)
  }
}



